<template>
    <section>
        <loader :isVisible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CustomField" SubModuleCode="CustomField" :LegendArray="[]"
            :showCheckBox="true" IdentityColumn="custom_field_id" :listType="['List']" :ListData="FieldsData"
            :HeaderText="$t('Custom_Fields')" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :widgets="widgets"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :TotalRecords="TotalRecords" :SortExp="SortExp" :SortBy="SortBy" v-on:PagerButtonClick="pagerMethod"
            v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')"
            v-on:ActionButtonClick="actionButtonClick" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:LeftsearchButtonClick="leftCommonSearch">

            <template v-slot:slotdata="{ data }">

                <template v-if="data.column.COLUMN_NAME == 'name'">
                    <span v-if="data.row.data_type_list.length > 0"
                        v-bind:class="data.row.data_type_list[0].class_name">&nbsp; &nbsp; {{ data.row.name }} </span>
                    <span v-else>&nbsp; &nbsp; {{ data.row.name }} </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'total_layout'">
                    <template v-if="data.row.total_layout > 0">
                        <span class="fa fa-eye text-info action-icon" style="font-size: 18px;"
                            v-on:click="viewCustomLayoutList(data.row.custom_layout_list, data.row.custom_field_id)"></span>
                    </template>
                    <template else>
                        <span>
                            <h4 v-if="data.row.total_layout == 0" class="fa fa-eye  action-icon" style="font-size: 18px;">
                            </h4>
                        </span>
                    </template>
                </template>
            </template>
        </tg-list>

        <OpenAddCustomFieldPopup v-if="showCustomfieldPopup" :ModuleId="ModuleId" :SubModuleId="SubModuleId"
            :LanguageArrayData="LanguageArray"></OpenAddCustomFieldPopup>
        <CloneField v-if="isShowCloneFieldPopup" :CustomFieldId="CustomFieldId" :CloneCustomField="CloneCustomField" :CloneDisplayname="CloneDisplayname"></CloneField>
        <DependencyField v-if="isShowDependencyFieldPopup" :DependencyData="DependencyData" :CustomFieldId="CustomFieldId">
        </DependencyField>
        <EditCustomField v-if="isShowEditCustomField" :FieldName="FieldName" :CustomFieldArray="CustomFieldArray"
            :LanguageArray="LanguageArray">
        </EditCustomField>
        <CustomLayoutList v-if="isShowCustomLayoutList" :CustomLayoutList="CustomLayoutList"></CustomLayoutList>
        <AddModuleAndSubModuelVue v-if="isShowModulePopup" @openCustomField="openCustomField"></AddModuleAndSubModuelVue>
        <ShowCustomFieldsDetails v-if="isShowCustomfieldDetail" :CustomFieldList="CustomFieldList">
        </ShowCustomFieldsDetails>
   
        
    </section>
</template>

<script>
import Schema from '../LayoutCustomFields/CustomFieldsSchema.js'
import DataService from "../../services/DataService.js";
import OpenAddCustomFieldPopup from '../../components/Common/Modal/AddCustomFieldsPopup.vue';
import EditCustomField from './EditCustomField.vue';
import CloneField from './CloneField.vue';
import DependencyField from './DependencyField.vue';
import CustomLayoutList from './CustomLayoutList.vue';
import AddModuleAndSubModuelVue from './AddModuleAndSubModuel.vue';
import ShowCustomFieldsDetails from './ShowCustomFieldsDetails.vue';
import loader from '../../components/Common/Loader.vue'


export default {

    components: {
        OpenAddCustomFieldPopup,
        CloneField,
        EditCustomField,
        DependencyField,
        CustomLayoutList,
        AddModuleAndSubModuelVue,
        loader,
        ShowCustomFieldsDetails
    },
    data() {
        return {
            isLoading: false,
            widgets: [],
            FieldName: '',
            isShowCustomfieldDetail: false,
            isShowEditCustomField: false,
            isShowModulePopup: false,
            PageSize: 10,
            PageNumber: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            customFieldName: null,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,
            searchCondition: '',
            TotalRecords: 0,
            CustomlayoutId: '',
            FieldsData: [],
            showCustomfieldPopup: false,
            module_id: '',
            sub_module_id: '',
            newLanguageArray: [],
            LanguageArray: [],
            isShowCloneFieldPopup: false,
            CustomFieldId: null,
            isShowDependencyFieldPopup: false,
            CustomFieldArray: [],
            isShowCustomLayoutList: false,
            CustomLayoutList: [],
            ModuleId: null,
            SubModuleId: null,
            DependencyData: [],
            CustomFieldList: [],
            CloneCustomField:'',
            CloneDisplayname:'',
        }
    },

    created: function () {
        var vm = this;
        vm.FetchData();
        vm.getLanguageList();
        vm.leftSearchFields[1].onChange = vm.getClickEvent;
        Schema.bindleftCommonSearchdropdown(vm, "CustomField", "module");

    },
    methods:
    {

        viewCustomLayoutList(event, custom_field_id) {
            var vm = this;
            if (custom_field_id != null || custom_field_id != undefined) {
                vm.CustomLayoutList = event;
                vm.isShowCustomLayoutList = true;
            }
            else {
                return vm.isShowCustomLayoutList = false;

            }

        },

        getClickEvent: function () {
            var vm = this;
            var moduleId = this.leftSearchFields[1].value
            Schema.bindleftCommonSearchdropdown(vm, "CustomField", "subModule", moduleId);

        },

        async getLanguageList() {
            var vm = this;
            DataService.GetLanguageList().then((response) => {
                if (response.data.status == "Success") {
                    let obj = { lang_name: '', lang_id: '' }
                    vm.newLanguageArray = response.data.result.DATA;
                    vm.newLanguageArray.forEach(element => {
                        obj = {
                            lang_name: element.LANG_NAME,
                            lang_id: element.LANG_ID
                        };
                        vm.LanguageArray.push(obj);
                    });
                }
            })
        },

        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            var params = `module_id=${vm.module_id}&sub_module_id=${vm.sub_module_id}&searchCondition=${vm.searchCondition}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`
            DataService.GetCustomUserDefinedCustomFieldListing(params).then(function (response) {
                vm.isLoading = false;
                if (JSON.parse(response.data) != null) {
                    vm.FieldsData = JSON.parse(response.data).data;
                    vm.FieldsData.forEach(row => {
                        row.isCheckBoxDisabled = false;
                        if (row.is_disabled == 1) {
                            row.isCheckBoxDisabled = true;
                        }
                        else {
                            row.isCheckBoxDisabled = false;
                        }

                    })
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'name') {
                            column.settings = {
                                isInSlot: true,
                                clickEvent: vm.clickEventOfColumn,

                            }
                        }
                        if (column.COLUMN_NAME == 'module_name') {
                            column.settings = {

                            }
                        }
                        if (column.COLUMN_NAME == 'sub_module_name') {
                            column.settings = {

                            }
                        }
                        if (column.COLUMN_NAME == 'length') {
                            column.settings = {

                            }
                        }
                        if (column.COLUMN_NAME == 'is_disabled') {
                            column.settings = {
                                isInSlot: true,
                            }

                        }
                        if (column.COLUMN_NAME == 'total_layout') {
                            column.settings = {
                                isInSlot: true,
                            }

                        }



                    })
                    if (vm.FieldsData != null) {
                        vm.TotalRecords = vm.FieldsData[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;

                    }
                    else {
                        vm.noRecord = true;
                        vm.FieldsData = [];
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }

                }
                else {
                    vm.noRecord = true;
                    vm.FieldsData = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }

                //setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                //setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);

            })
            // vm.isLoading = false;
        },

        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Preview':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Dependency':
                        action.isVisible = false;
                        break;
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isDisabled = (row.is_disabled == 1) ? true : false;
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'OpenView':
                    vm.OpenView(item);
                    break;
                case 'EditCustomField':
                    vm.EditCustomField(item);
                    break;
                case 'DependencyField':
                    vm.DependencyField(item);
                    break;
                case 'CloneField':
                    vm.AddCloneField(item);
                    break;
                case 'DeleteSingleRecord':
                    vm.commonDeleteData(item.custom_field_id);
                    break;
            }
        },

        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "GetQuickAdd":
                    this.GetQuickAdd(event);
                    break;
                case "AddCustomField":
                    this.AddCustomField(event);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "Customlayoutlisting":
                    this.Customlayoutlisting();
                    break;
            }
        },
       
        Customlayoutlisting: function () {
            this.$router.push({
                name: 'CustomLayoutListing'
            });
        },
        AddCloneField: function (event) {
            var vm = this;
            if (event.custom_field_id != null || event.custom_field_id != undefined) {
                vm.CustomFieldId = event.custom_field_id;
                vm.CloneCustomField=event.name;
                vm.CloneDisplayname=event.display_name;
                return vm.isShowCloneFieldPopup = true;
            }
            else {
                return vm.isShowCloneFieldPopup = false;

            }


        },

        OpenView(item) {

            var vm = this;
            if (item.custom_field_id != null || item.custom_field_id != undefined) {
                vm.CustomFieldList = item;

                return vm.isShowCustomfieldDetail = true;
            }
            else {
                return vm.isShowCustomfieldDetail = false;

            }

        },

        CloseAddCustomFieldPoup() {
            var vm = this;
            vm.showCustomfieldPopup = false;
            vm.FetchData();

        },

        clickEventOfColumn: function (row, column) {
            if (column.COLUMN_NAME == 'name') {
                // this.$router.push({
                //     name: 'ManageCustomLayout',
                //     params: { CustomlayoutId: row.CUSTOM_LAYOUT_ID }
                // });
            }
        },

        EditCustomField: function (event) {
            var vm = this;
            if (event.custom_field_id != null || event.custom_field_id != undefined) {
                vm.CustomFieldArray = event;
                vm.FieldName = event.name;
                return vm.isShowEditCustomField = true;
            }
            else {
                return vm.isShowEditCustomField = false;

            }



        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;

            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = { ids: val, CRM: "", Deals: "", refCode: "talygen_custom_field" }

                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        var box = "";
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            vm.FetchData();
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "Success", true, vm.$t("Alert"));
                        }
                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        async DependencyField(event) {
            var vm = this;
            vm.customFieldName = event.name;
            if (event.custom_field_id != null || event.custom_field_id != undefined) {
                vm.CustomFieldId = event.custom_field_id;
                await vm.MappingLookupList();

                return vm.isShowDependencyFieldPopup = true;
            }
            else {
                return vm.isShowDependencyFieldPopup = false;

            }


        },
        async MappingLookupList() {
            var vm = this;
            let resData = []
            var params = `customFieldId=${vm.CustomFieldId}`
            await DataService.GetCustomFieldShowWhenDDL(params).then((response) => {
                resData = JSON.parse(response.data);
                vm.DependencyData = resData;
            })

        },
        AddCustomField: function (ths) {
            var vm = this;
            vm.isShowModulePopup = true;
        },
        GetQuickAdd : function (ths){
            var vm = this;
            vm.QuickAddpopup=true;
        },

        openCustomField(event) {
            let vm = this;
            vm.ModuleId = event.ModuleName;
            vm.SubModuleId = event.SubModuleName;
            vm.isShowModulePopup = false
            vm.showCustomfieldPopup = true
        },


        hidePopup() {
            var vm = this;
            vm.showCustomfieldPopup = false;
            vm.FetchData();

        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },


        CloseAddPopup: function () {
            var vm = this;
            vm.showLayoutPopup = false;
            vm.FetchData();

        },

        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

    }
}
</script>

<style scoped>
/* .is-visible {
    display: inline-block;
    height: auto;
} */
</style>